@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #606c88;  
  background: -webkit-linear-gradient(to right, #3f4c6b, #606c88);  
  background: linear-gradient(to right, #3f4c6b, #606c88); 
  /* background-color: rgb(0, 0, 0,.3)!important; */
  /* background-color: #ebebeb!important; */
  /* box-shadow: 0 4px 4px #00000040; */
  
}

 /* width */
 ::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #606c88;  
  background: -webkit-linear-gradient(to right, #3f4c6b, #606c88);  
  background: linear-gradient(to right, #3f4c6b, #606c88); 
  

}

/* Handle */
::-webkit-scrollbar-thumb {
  background: linear-gradient(to right, #eef2f3, #8e9eab);
  border-radius: 20px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(31, 245, 166);
}


* {
scrollbar-color:  #ffffff  dodgerblue !important;

    scrollbar-width: thin !important;
}




code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.links-box-active {
  display: block!important;
}

.box-position-active {
   top: 21%!important;
}

.rotate-zero {
  transform: rotateZ(0deg);
  transition: all 1s ease;
}

.about-div {
  padding-top: 20px;
}

.navLinks_wrapper_active {
    width: 430px!important;
    height: 430px!important;
    transform: rotateZ(360deg);
}


.skills-rotate {
  transform: rotateZ(90deg);
  transition: all 1s ease;
}
.skills-rotate .skills-div {
  transform: rotateZ(-90deg)  translateX(10px);
  transition: all 1s ease;
}

.skills-rotate .certificate-div {
  transform: rotateZ(-90deg) translateX(35px);
  transition: all 1s ease;
}

.skills-rotate .my_img {
  transform: rotateZ(-90deg);
  transition: all 1s ease;
}

.skills-rotate .about-div {
  transform: rotateZ(-90deg) translateX(-12px);
  transition: all 1s ease;
  padding-top: 0px;
}

.skills-rotate .projects-div {
  transform: rotateZ(-90deg)  translateY(20px);;
  transition: all 1s ease;
}



/* About */
.about-rotate {
  transform: rotateZ(0deg);
  transition: all 1s ease;
}

.about-rotate .about-div {
  transform: rotateZ(0deg);
  transition: all 1s ease;
}

.about-div .skills-div {
  transform: rotateZ(0deg);
  transition: all 1s ease;
}

/* project */
.projects-rotate-basic {
  transform: rotateZ(-90deg);
  transition: all 1s ease;
}
.projects-rotate-basic .skills-div {
  transform: rotateZ(90deg) translateY(20px);
  transition: all 1s ease;
}

.projects-rotate-basic .certificate-div {
  transform: rotateZ(90deg) translateX(-38px);
  transition: all 1s ease;
}

.projects-rotate-basic .my_img {
  transform: rotateZ(90deg);
  transition: all 1s ease;
}

.projects-rotate-basic .about-div {
  transform: rotateZ(90deg) translateX(15px);
  transition: all 1s ease;
  padding-top: 0px;
}

.projects-rotate-basic .projects-div {
  transform: rotateZ(90deg);
  transition: all 1s ease;
}

/* certificate */

/* .certificate-from-projects {
  transform: rotateZ(-90deg);
  transition: all 1s ease;
} */

.certificate-rotate-from-about {
  transform: rotateZ(180deg);
}


.certificate-rotate-from-about .skills-div {
  transform: rotateZ(-180deg) translateX(25px);
  transition: all 1s ease;
  /* width: fit-content; */
}

.certificate-rotate-from-about .certificate-div {
  transform: rotateZ(-180deg) translateY(20px);
  transition: all 1s ease;
}

.certificate-rotate-from-about .my_img {
  transform: rotateZ(-180deg);
  transition: all 1s ease;
}

.certificate-rotate-from-about .about-div {
  transform: rotateZ(-180deg) translateX(-12px);
  transition: all 1s ease;
  padding-top: 0px;
}

.certificate-rotate-from-about .projects-div {
  transform: rotateZ(-180deg);
  transition: all 1s ease;
}


.certificate-rotate-from-projects {
  transform: rotateZ(-180deg);
  transition: all 1s ease;
}

.certificate-rotate-from-projects .skills-div {
  transform: rotateZ(180deg)  translateX(25px);
  transition: all 1s ease;
}

.certificate-rotate-from-projects .certificate-div {
  transform: rotateZ(180deg) translateY(20px);
  transition: all 1s ease;
}

.certificate-rotate-from-projects .my_img {
  transform: rotateZ(180deg);
  transition: all 1s ease;
}

.certificate-rotate-from-projects .about-div {
  transform: rotateZ(180deg) translateX(-12px);
  transition: all 1s ease;
  padding-top: 0px;
}

.certificate-rotate-from-projects .projects-div {
  transform: rotateZ(180deg)  translateY(0px);;
  transition: all 1s ease;
}


.certificate-rotate-from-skills {
  transform: rotateZ(180deg);
  transition: all 1s ease;
}

.certificate-rotate-from-skills .skills-div {
  transform: rotateZ(-180deg)  translateX(20px);
  transition: all 1s ease;
}

.certificate-rotate-from-skills .certificate-div {
  transform: rotateZ(-180deg) translateY(20px);
  transition: all 1s ease;
}

.certificate-rotate-from-skills .my_img {
  transform: rotateZ(-180deg);
  transition: all 1s ease;
}

.certificate-rotate-from-skills .about-div {
  transform: rotateZ(-180deg) translateX(-12px);
  transition: all 1s ease;
  padding-top: 0px;
}

.certificate-rotate-from-skills .projects-div {
  transform: rotateZ(-180deg)  translateY(0px);;
  transition: all 1s ease;
}


@media (max-width:500px) {
  .box-position-active {
    top: 30% !important;
}

nav a {
  font-size: 18px!important;
}

.navLinks_wrapper_active {
  width: 250px!important;
  height: 250px!important;
}
}

.arrow-pointer {
  position: absolute;
  left: 48px;
  top: -10px;
}

.arrow-pointer-animation {
  animation-name: colorfullarrow;
  animation-duration: 2s;
  color: #dbdbdb;
  animation-iteration-count: infinite;
}

@keyframes colorfullarrow {
  from {
    color: #070707;
  }

  to {
    color: #ffffff;
  }
}